//note 数据适配方法
const util={};
util.dataAdapter = function (data, arr1, arr2, flag) {
  let dll = {};
  _.each(arr1, (v1,i) => {
    _.each(arr2, (v2,k) => {
      if (i === k) {
        dll[v1] = v2;
      }
    });
  });
  const str = JSON.stringify(data);
  const reg = eval('/(' + arr1.join('|') + ')/g');
  const result = str.replace(reg, ($0, $1) => {
    return dll[$1];
  });
  let last = '';
  if (flag === false) {
    last = _.assign(data, JSON.parse(result));
  } else {
    last = JSON.parse(result);
  }
  return last;
};
export default util;



