import request from '../libs/request'

//查询
export async function queryApi(url,params,mock,method) {
  return request(url,{
    mock:mock ? mock : false,
    body:{
      data:params,
      method:method ? method : 'POST'
    }
  });
}


export async function queryApis(url,params,mock,method) {
  return request(url,{
    mock:mock ? mock : false,
    body:{
      data:params,
      method:method ? method : 'POST'
    }
  });
}



