<template>
  <ht-base-layout>
    <ht-base-header>
      <div class="header-logo">数据管理平台V1.0.0</div>
      <ht-base-menu class="header-nav" mode="horizontal" theme="primary">
        <ht-base-menu-item v-for="(item,index) in tabListHeader" :name="item.name" :key="index">
          <router-link :to="item.path">
            {{item.title}}
          </router-link>
        </ht-base-menu-item>
      </ht-base-menu>
      <ht-base-dropdown trigger="click" class="header-user" @on-click="information">
        <div class="header-user-in">
          <ht-base-avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" size="large" style="margin-right: 8px"/>
          {{this.userInfo.userName}}
        </div>
        <ht-base-dropdown-menu slot="list">
          <ht-base-dropdown-item name="1">个人信息</ht-base-dropdown-item>
          <ht-base-dropdown-item name="2">退出登录</ht-base-dropdown-item>
        </ht-base-dropdown-menu>
      </ht-base-dropdown>
    </ht-base-header>
    <ht-base-content>
      <div class="content-left">
        <ht-base-menu mode="vertical" theme="light" :active-name="activeName"
                      style="width:256px;min-height: 100%">
          <ht-base-menu-item v-for="(item,index) in tabList" :name="item.name" :key="index">
            <router-link :to="item.path">
              <ht-base-icon :type='item.icon' size=14 style="margin-right: 10px"/>
              {{item.title}}
            </router-link>
          </ht-base-menu-item>
        </ht-base-menu>
      </div>
      <div class="content-right">
        <ht-base-breadcrumb>
          当前位置：单据信息管理
          <ht-base-breadcrumb-item></ht-base-breadcrumb-item>
          <ht-base-breadcrumb-item>{{this.breakName}}</ht-base-breadcrumb-item>
        </ht-base-breadcrumb>
        <router-view id="myrame"></router-view>
      </div>
    </ht-base-content>
    <!--个人信息-->
    <ht-base-modal v-model="userInfoForm" title="个人信息" footer-hide>
      <ht-base-form :label-width="100">
        <ht-base-form-item label="头像:">
          <ht-base-avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" size="large"/>
          <span style="display: inline-block;margin-left: 20px;"><a @click="showModifyPwdForm" target="_blank">修改密码</a></span>
        </ht-base-form-item>
        <ht-base-form-item label="姓名:">
          <div>{{this.userInfo.userName}}</div>
        </ht-base-form-item>
        <ht-base-form-item label="角色:">
          <div>{{this.userInfo.roleName}}</div>
        </ht-base-form-item>
        <ht-base-form-item label="手机:">
          <div>{{this.userInfo.linkMobile}}</div>
        </ht-base-form-item>
        <ht-base-form-item label="邮箱:">
          <div>{{this.userInfo.email}}</div>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
    <!--修改密码-->
    <ht-base-modal v-model="modifyPwdForm" title="修改密码" footer-hide>
      <ht-base-form class="modal-form" ref="formCustoms" :model="formCustoms" :rules="ruleCustoms" :label-width="80">
        <ht-base-form-item label="密码" prop="passwd">
          <ht-base-input type="password" v-model="formCustoms.passwd" placeholder="请输入密码"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="确认密码" prop="passwdCheck">
          <ht-base-input type="password" v-model="formCustoms.passwdCheck" placeholder="请二次确认密码"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item class="beforeBut modal-footer">
          <ht-base-button type="primary" ghost @click="hideModifyPwdForm('formCustoms')">取消</ht-base-button>
          <ht-base-button type="primary" @click="modifyPwd('formCustoms')">确定</ht-base-button>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
    <!--参数信息配置-->
    <ht-base-modal v-model="configForm" title="参数配置" footer-hide>
      <ht-base-form class="modal-form" ref="configInfo" :model="configInfo" :label-width="150">
        <ht-base-form-item label="百度Token接口地址" prop="baiduTokenUrl">
          <ht-base-input v-model="configInfo.baiduTokenUrl" placeholder="请输入百度Token接口地址"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="百度IOCR接口地址" prop="baiduIocrUrl">
          <ht-base-input v-model="configInfo.baiduIocrUrl" placeholder="请输入百度IOCR接口地址"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="应用API Key" prop="clientId">
          <ht-base-input v-model="configInfo.clientId" placeholder="请输入应用API Key"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="应用Secret Key" prop="secretKey">
          <ht-base-input v-model="configInfo.secretKey" placeholder="请输入应用Secret Key"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="模板ID" prop="templateSign">
          <ht-base-input v-model="configInfo.templateSign" placeholder="请输入模板ID"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="模板ID" prop="templateSign">
          <ht-base-input v-model="configInfo.templateSign" placeholder="请输入模板ID"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item class="modal-footer">
          <ht-base-button type="primary" ghost @click="hideConfigForm('formCustoms')">取消</ht-base-button>
          <ht-base-button type="primary" @click="changeConfigInfo('formCustoms')">确定</ht-base-button>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
  </ht-base-layout>
</template>

<script>
    import '../../themes/default/custom.less'
    import {queryApi} from "../../service/api"
    import {Notice} from 'iview'

    export default {
        data() {
            const validatePasss = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码！'));
                } else {
                    if (this.formCustoms.passwdCheck !== '') {
                        // 对第二个密码框单独验证
                        this.$refs.formCustoms.validateField('passwdCheck');
                    }
                    callback();
                }
            };
            const validatePassChecks = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请确认密码！'));
                } else if (value !== this.formCustoms.passwd) {
                    callback(new Error('两次输入的密码不一致'));
                } else {
                    callback();
                }
            };
            return {
                isAdmin: false,
                userInfoForm: false, //个人信息
                modifyPwdForm: false,
                configForm : false,
                activeNameHeader: 'menu1',    //绑定头部数据名称
                userInfo: {},     //数据存放地
                configInfo: {},
                activeName: '1',    //绑定侧边数据名称
                tabList: [         //初始化数据
                    {
                        name: "commodityIndex",
                        icon: "LiRa_edit1",
                        title: "单据信息管理",
                        path: '/commodityIndex'
                    }
                ],
                tabListHeader : [
                    {
                        name: "commodityIndex",
                        title: "单据信息管理",
                        path: '/commodityIndex'
                    }
                ],
                breakName: "基础数据管理",  //初始化面包屑数据
                configModals: false,
                formCustoms: {
                    passwd: '',
                    passwdCheck: ''
                },
                ruleCustoms: {
                    passwd: [
                        {required: true, validator: validatePasss, trigger: 'blur'},
                        {
                            pattern: /^(?=.*[0-9])(?=.*[a-z])(?=.*[^a-zA-Z0-9]).{8,16}$/,
                            message: '密码为8-16位，包含字母、数字、特殊字符！'
                        }
                    ],
                    passwdCheck: [
                        {required: true, validator: validatePassChecks, trigger: 'blur'}
                    ]
                },
                roleId:'',    //权限值
            }
        },
        created(){
            //菜单高亮——刷新
            if (this.$route.meta.menuFocus) {
                this.activeNameHeader = this.$route.meta.menuFocus
                this.activeName = this.$route.meta.menuSecondFocus
                this.breakName = this.$route.meta.menuTitle
            }
        },
        watch: {
            '$route.path': function (newVal, oldVal) {
                //如果没有定义meta的
                if (this.$route.meta.menuFocus) {
                    this.activeNameHeader = this.$route.meta.menuFocus
                    this.$nextTick(()=>{
                        this.activeName = this.$route.meta.menuSecondFocus
                        this.breakName = this.$route.meta.menuTitle
                    })
                }
            },
            activeNameHeader(newVal,oldVal){
            },
            $route: {
                // val是改变之后的路由，oldVal是改变之前的val
                handler: function(val, oldVal){
                    if(this.roleId == 0){
                        if(val.path =='/userIndex'){
                            window.location.href = '../../../static/login/403.html'
                        }
                    }
                },
                // 深度观察监听
                deep: true
            }
        },
        mounted() {
            this.getUserInfo();
        },
        methods: {
            getUserInfo() {
                queryApi('api/user/getUserInfo', {}).then((res) => {
                    if (res !== undefined) {
                        this.userInfo.userName = res.userName;
                        this.userInfo.linkMobile = res.linkMobile;
                        this.userInfo.email = res.email;
                        this.roleId = res.roleId;

                      // res.roleId = 1;

                        if(res.roleId == 1){
                            this.userInfo.roleName = '管理员';
                        }
                        else{
                            this.userInfo.roleName = '普通用户';
                        }

                        if (res.roleId == 1) {
                            this.isAdmin = true;
                            this.tabList = [
                                {
                                    name: "commodityIndex",
                                    icon: "LiRa_edit1",
                                    title: "单据信息管理",
                                    path: '/commodityIndex'
                                },
                                {
                                    name: "userIndex",
                                    icon: "LiRa_edit1",
                                    title: "用户管理",
                                    path: '/userIndex'
                                },
                                {
                                  name: "configIndex",
                                  icon: "LiRa_edit1",
                                  title: "配置信息管理",
                                  path: '/configIndex'
                                }
                            ]
                        } else {
                            this.tabListHeader = [
                                {
                                    name: "commodityIndex",
                                    title: "单据信息管理",
                                    path: '/commodityIndex'
                                }
                            ]
                        }
                    }
                })
            },
            //用户信息
            information(name) {
                if (name == 1) {
                    this.getUserInfo();
                    this.userInfoForm = true;
                }
                else if (name == 2) {
                    this.$HtBaseModal.confirm({
                        title: "您确认要退出么？",
                        onOk() {
                            queryApi('api/logout', {}).then((res) => {
                                window.location.href = '../../../static/login/index.html'
                            });
                        },
                        cancelText: '取消',
                    });
                }
            },
            //修改密码 服务
            modifyPwd(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        queryApi('api/user/modifyPwd', {
                            password: this.formCustoms.passwdCheck
                        }).then((res) => {
                            if (res !== undefined) {
                                Notice.success({
                                    title: '系统提示',
                                    desc: '密码修改成功'
                                });
                                setTimeout(function(){
                                    window.location.href = '../../../static/login/index.html'
                                }, 1500);
                            }
                        });
                    }
                })
            },
            showModifyPwdForm() {
                this.modifyPwdForm = true;
            },
            hideModifyPwdForm() {
                this.modifyPwdForm = false;
            }
        }
    }
</script>

<style scoped xml:lang="less">
  .modal-form .beforeBut {
    position: absolute;
    height: 72px;
    width: 100%;
    left: 0;
    bottom: -82px;
    padding: 20px;
    background: #fff;
    border-radius: 0 0 6px 6px;
  }
</style>



