import Vue from 'vue'
import Router from 'vue-router'
import commodityIndex from '../pages/commodity/commodityIndex.vue'
import userIndex from '../pages/users/userIndex.vue'
import configIndex from '../pages/config/configIndex.vue'

Vue.use(Router)

function importRoutes(r) {
  let routes = r.keys().map(key => r(key).default);
  return _.flatten(routes);
}

export default new Router({
  routes: [
    {
      // 初始化页面展示
      path: '/',
      redirect: 'commodityIndex',
    },
    {
      path: '/userIndex',
      name: 'userIndex',
      component: userIndex,
      meta:{
        menuFocus:"userIndex",
        menuSecondFocus:"userIndex",
        menuTitle:'用户管理'
      },
    },
    {
      path: '/configIndex',
      name: 'configIndex',
      component: configIndex,
      meta:{
        menuFocus:"configIndex",
        menuSecondFocus:"configIndex",
        menuTitle:'配置信息管理'
      },
    },
    {
      path: '/commodityIndex',
      name: 'commodityIndex',
      component: commodityIndex,
      meta:{
        menuFocus:"commodityIndex",
        menuSecondFocus:"commodityIndex",
        menuTitle:'单据信息管理'
      },
    }
  ]
})



