<template>
  <div class="table">
    <!--头部按钮-->
    <div class="button">
        <ht-base-button @click="add('formValidate')" type="primary">新增用户</ht-base-button>
      <div class="search">
        <ht-base-input v-model="userName" placeholder="用户名" style="width:200px;margin-left: 5px" @keyup.enter.native="queryList()"/>
        <ht-base-input v-model="email" placeholder="邮箱" style="width:200px;margin-left: 5px" @keyup.enter.native="queryList()"/>
        <ht-base-input v-model="linkMobile" placeholder="手机号" style="width:200px;margin-left: 5px" @keyup.enter.native="queryList()"/>
        <ht-base-button type="info" @click="queryList()">搜索</ht-base-button>
      </div>
    </div>

    <ht-base-modal v-model="addModal" :title="business" footer-hide >
      <ht-base-form class="modal-form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120" style="height: 400px;">
        <ht-base-form-item label="姓名" prop="userName">
          <ht-base-input placeholder="请输入姓名" v-model="formValidate.userName" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="密码" prop="password">
          <ht-base-input placeholder="请输入密码" v-model="formValidate.password" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="邮箱" prop="email">
          <ht-base-input v-model="formValidate.email" placeholder="请输入邮箱"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="手机号" prop="linkMobile">
          <ht-base-input v-model="formValidate.linkMobile" placeholder="请输入手机号"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="绑定配置信息" prop="configId">
          <ht-base-select v-model="formValidate.configId" :label-in-value="true" @on-change="configSelect">
            <ht-base-option v-for="item in configList" :value="item.id" :label="item.name" :key="item.id">
              {{item.name }}
            </ht-base-option>
          </ht-base-select>
        </ht-base-form-item>
        <ht-base-form-item label="锁定账号" prop="locked" filterable>
            <ht-base-switch size="large" v-model="formValidate.locked">
                <span slot="open">是</span>
                <span slot="close">否</span>
            </ht-base-switch>
        </ht-base-form-item>
        <ht-base-form-item class="modal-footer">
          <ht-base-button type="primary" ghost @click="cancel('formValidate')">取消</ht-base-button>
          <ht-base-button type="primary" @click="handleSubmit('formValidate')">确定</ht-base-button>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
    <!--table内容-->
    <vxe-grid
      border
      show-overflow
      show-header-overflow
      highlight-hover-row
      highlight-current-row
      ref="xGrid"
      :height="tableHeight"
      :data="tableData"
      :columns="tableColumn"
      :loading="loading"
      :start-index="(tablePage.currentPage - 1) * tablePage.pageSize"
      :pager-config="tablePage"
      @page-change="handlePageChange"
      :edit-config="{trigger: 'click'}">
      <template v-slot:empty>
            <span style="color: red;">
              <img src="../../../static/img/img1.gif">
              <p>不用再看了，没有更多数据了！</p>
            </span>
      </template>
    </vxe-grid>
  </div>
</template>
<script>
    import {queryApi} from "../../service/api";

    export default {
        data() {
            return {
                tableHeight:'350',
                userName: '',
                email: '',
                linkMobile: '',
                addModal: false,
                business:'新增用户',
                formValidate: {
                    userName: '',
                    password:'',
                    email:'',
                    linkMobile:'',
                    configId:'',
                    configName:'',
                    locked:''
                },
                configList:[],
                ruleValidate: {
                  userName:[
                      {required: true, message: '姓名不能为空', trigger: 'change'}
                  ],
                  password:[
                      {required: true, message: '密码不能为空', trigger: 'change'}
                  ]
                },
                tableData: [],   //列表数据
                loading: false,   //加载状态
                tablePage: {     //分页
                    total: 0,     //总条数
                    currentPage: 1,  //当前页
                    pageSize: 10,   //每页大小
                    align: 'left',   //对齐方式
                    pageSizes: [10, 20, 50, 100, 200, 500],  //每页大小选项列表
                    layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total'],   //布局
                    perfect: true   //样式
                },
                tableColumn: [     //table配置
                    {
                      type: 'seq',
                      title: '序号',
                      align: "center",
                      width: 50
                    },
                    {
                      field: 'action',
                      title: '操作',
                      align: 'center',
                      width: 160,
                      slots: {
                        default: ({row, column}) => {
                          let value = row["userName"];
                          if(value ==""){
                            return [
                              <ht-base-button type = "primary" onClick={ () => this.edit(row, column) }>编辑</ht-base-button>,
                              <ht-base-button style="margin-left:10px" type="primary" ghost onClick={ () => this.remove(row, column) }>删除</ht-base-button>
                            ]
                          }
                          else{
                            if (value.toLowerCase().indexOf("admin") != -1){
                              return [
                                <ht-base-button type = "primary" onClick={ () => this.edit(row, column) }>编辑</ht-base-button>
                              ]
                            }
                            else{
                              return [
                                <ht-base-button type = "primary" onClick={ () => this.edit(row, column) }>编辑</ht-base-button>,
                                <ht-base-button style="margin-left:10px" type="primary" ghost onClick={ () => this.remove(row, column) }>删除</ht-base-button>
                              ]
                            }
                          }
                        }
                      }
                    },
                    {
                        field: 'id',
                        title: '用户ID',
                        width: 80,
                        align: "center",
                        visible: false
                    },
                    {
                        field: 'userName',
                        title: '姓名',
                        width: 130,
                        align: "center",
                    },
                    {
                        field: 'email',
                        title: '邮箱',
                        align: "center",
                        width: 180
                    },
                    {
                        field: 'linkMobile',
                        title: '手机号',
                        align: "center",
                        width: 140
                    },
                    {
                      field: 'configName',
                      title: '接口名',
                      align: "center",
                      width: 140
                    },
                    {
                        field: 'locked',
                        title: '是否锁定',
                        align: "center",
                        width:100,
                        slots: {
                            default: ({row, column}) => {
                                if (row.locked == 0) {
                                    return [
                                        <div>正常</div>
                                    ]
                                } else{
                                    return [
                                        <div style="color:red;">锁定</div>
                                    ]
                                }
                            }
                        }
                    },
                    {
                        field: 'createDate',
                        title: '创建时间',
                        align: "center",
                        width:200,
                        slots: {
                            default: ({row, column}) => {
                               if(row.createDate != null){
                                   return row.createDate.substring(0, 10)
                               }
                            }
                        }
                    }
                ],
            }
        },
        created() {
            this.tableHeight = window.innerHeight - 180;
            this.findList();  //table数据
            this.getConfigList();
        },
        methods: {
            //table数据
            findList() {
              this.loading = true;
                queryApi('api/user/getUserList', {
                    pageNo: this.tablePage.currentPage,
                    pageSize: this.tablePage.pageSize,
                    userName: this.userName,
                    email: this.email,
                    linkMobile: this.linkMobile
                }).then((res) => {
                  if (res.total === 0 || res.total === null) {
                    this.tableData = []
                    this.tablePage.total = 0;
                    this.tablePage.currentPage = 1;
                  } else {
                    this.tablePage.total = res.total;
                    this.tableData = res.list;
                  }
                  this.loading = false;
                });
            },
            getConfigList() {
              queryApi('api/config/getConfigListNoPage', {
              }).then((res) => {
                this.configList = res;
              });
            },
            //所属角色 ID Name
            configSelect(value) {
              this.formValidate.configId = value.value;
              this.formValidate.configName = value.label;
            },
            //分页跳转
            handlePageChange({currentPage, pageSize}) {
                this.tablePage.currentPage = currentPage;
                this.tablePage.pageSize = pageSize;
                this.findList()
            },
            //搜索
            queryList() {
                this.tablePage.currentPage=1;
                this.findList();
            },
            //新增
            add(name) {
                this.addModal = true;
                this.formValidate.id = '';
                this.formValidate.userName = '';
                this.formValidate.password = '';
                this.formValidate.email = '';
                this.formValidate.linkMobile='';
                this.formValidate.configId='';
                this.formValidate.locked = 0;
                this.business="新增用户";
                this.$refs[name].resetFields();
            },
            handleSubmit(name) {
                if (this.formValidate.id != "") {
                    this.$refs[name].validate((valid) => {
                        if (valid) {
                            if(this.formValidate.locked){
                                this.formValidate.locked = 1;
                            }
                            else{
                                this.formValidate.locked = 0;
                            }
                            queryApi('api/user/editUser', (this.formValidate)).then((res) => {
                                if (res !== undefined) {
                                    this.addModal = false;
                                    this.$refs[name].resetFields();
                                    this.$HtBaseMessage.success('修改成功!');
                                    this.findList();
                                } else {
                                    this.$HtBaseMessage.error('修改失败!');
                                }
                            });
                        }
                    })
                } else {
                    this.$refs[name].validate((valid) => {
                        if (valid) {
                            if(this.formValidate.locked){
                                this.formValidate.locked = 1;
                            }
                            else{
                                this.formValidate.locked = 0;
                            }
                            queryApi('api/user/addUser', (this.formValidate)).then((res) => {
                                if (res !== undefined) {
                                    this.$refs[name].resetFields();
                                    this.$HtBaseMessage.success('添加成功!');
                                    this.addModal = false;
                                    this.findList();
                                } else {
                                    this.$HtBaseMessage.error('添加失败!');
                                }
                            });
                        }
                    })
                }
            },
            //新增修改关闭modal
            cancel(name) {
                this.$refs[name].resetFields();
                this.addModal = false;
            },
            //编辑
            edit(row) {
                this.$refs['formValidate'].resetFields();
                this.addModal = true;
                this.business="编辑用户";
                this.formValidate.id = row.id;
                queryApi('api/user/queryById', {
                    id: this.formValidate.id,
                }).then((res) => {
                    if (res !== false) {
                        this.formValidate.id = this.formValidate.id;
                        this.formValidate.password = res.password;
                        this.formValidate.userName = res.userName;
                        this.formValidate.email = res.email;
                        this.formValidate.configId = res.configId;
                        this.formValidate.linkMobile = res.linkMobile;
                        if(res.locked == 1){
                            this.formValidate.locked = true;
                        }
                        else{
                            this.formValidate.locked = false;
                        }
                    }
                });
            },
            //删除
            remove(row) {
                let that=this;
                var ids = [];
                ids.push(row.id);
                that.$HtBaseModal.confirm({
                    title: "您确认要删除么？",
                    onOk() {
                        queryApi('api/user/deleteById', {
                            ids: JSON.parse(ids),
                        }).then((res) => {
                            if (res != false) {
                                that.findList();
                                that.$HtBaseMessage.success('删除成功!');
                            }
                        });
                    },
                    cancelText: '取消',
                });
            }
        }
    }
</script>

<style scoped xml:lang="less">
  .modal-form{
    height: 350px;
  }
  .table {
    overflow: scroll;
  }

  .search {
    float: right;
    margin-right: 10px;
  }

  .button {
    width: 100%;
    display: block;
    padding: 17px 0;
  }
</style>



