<template>
  <div class="table">
    <!--头部按钮-->
    <div class="button">
        <ht-base-button @click="add('formValidate')" type="primary">新增配置</ht-base-button>
      <div class="search">
        <ht-base-input v-model="clientId" placeholder="请输入客户端ID" style="width:200px;margin-left: 5px" @keyup.enter.native="queryList()"/>
        <ht-base-input v-model="templateSign" placeholder="请输入模板ID" style="width:200px;margin-left: 5px" @keyup.enter.native="queryList()"/>
        <ht-base-button type="info" @click="queryList()">搜索</ht-base-button>
      </div>
    </div>

    <ht-base-modal v-model="addModal" :title="business" footer-hide>
      <ht-base-form class="modal-form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="140" style="height: 400px;">
        <ht-base-form-item label="接口名" prop="name">
          <ht-base-input placeholder="接口名" v-model="formValidate.name" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="客户端ID" prop="clientId">
          <ht-base-input placeholder="请输入客户端ID" v-model="formValidate.clientId" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="SecretKey" prop="secretKey">
          <ht-base-input placeholder="请输入SecretKey" v-model="formValidate.secretKey" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="百度Token接口地址" prop="baiduTokenUrl">
          <ht-base-input v-model="formValidate.baiduTokenUrl" placeholder="请输入百度Token接口地址"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="百度ICOR接口地址" prop="baiduIocrUrl">
          <ht-base-input v-model="formValidate.baiduIocrUrl" placeholder="请输入百度ICOR接口地址"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="模板ID" prop="templateSign">
          <ht-base-input v-model="formValidate.templateSign" placeholder="请输入模板ID"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item class="modal-footer">
          <ht-base-button type="primary" ghost @click="cancel('formValidate')">取消</ht-base-button>
          <ht-base-button type="primary" @click="handleSubmit('formValidate')">确定</ht-base-button>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
    <!--table内容-->
    <vxe-grid
      border
      show-overflow
      show-header-overflow
      highlight-hover-row
      highlight-current-row
      ref="xGrid"
      :height="tableHeight"
      :data="tableData"
      :columns="tableColumn"
      :loading="loading"
      :start-index="(tablePage.currentPage - 1) * tablePage.pageSize"
      :pager-config="tablePage"
      @page-change="handlePageChange"
      :edit-config="{trigger: 'click'}">
      <template v-slot:empty>
            <span style="color: red;">
              <img src="../../../static/img/img1.gif">
              <p>不用再看了，没有更多数据了！</p>
            </span>
      </template>
    </vxe-grid>
  </div>
</template>
<script>
    import {queryApi} from "../../service/api";

    export default {
        data() {
            return {
                tableHeight:'350',
                clientId: '',
                templateSign: '',
                addModal: false,
                business:'新增配置信息',
                formValidate: {
                  name: '',
                  clientId: '',
                  secretKey:'',
                  baiduTokenUrl:'',
                  baiduIocrUrl:'',
                  templateSign:''
                },
                ruleValidate: {
                    name:[
                      {required: true, message: '接口名不能为空', trigger: 'blur'}
                    ],
                    clientId:[
                        {required: true, message: '客户端ID不能为空', trigger: 'blur'}
                    ],
                    secretKey:[
                        {required: true, message: 'SecretKey不能为空', trigger: 'blur'}
                    ],
                    baiduTokenUrl:[
                      {required: true, message: '百度Token接口地址不能为空', trigger: 'blur'}
                    ],
                    baiduIocrUrl:[
                      {required: true, message: '百度IOCR接口地址不能为空', trigger: 'blur'}
                    ],
                    templateSign:[
                      {required: true, message: '模板ID', trigger: 'blur'}
                    ]
                },
                tableData: [],   //列表数据
                loading: false,   //加载状态
                tablePage: {     //分页
                    total: 0,     //总条数
                    currentPage: 1,  //当前页
                    pageSize: 10,   //每页大小
                    align: 'left',   //对齐方式
                    pageSizes: [10, 20, 50, 100, 200, 500],  //每页大小选项列表
                    layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total'],   //布局
                    perfect: true   //样式
                },
                tableColumn: [     //table配置
                    {
                        field: 'id',
                        title: '主键ID',
                        width: 80,
                        align: "center"
                    },
                    {
                      field: 'action',
                      title: '操作',
                      align: 'center',
                      width: 160,
                      slots: {
                        default: ({row, column}) => {
                          let value = row["userName"];
                          if(value == "admin"){
                            return [
                              <ht-base-button type = "primary" onClick={ () => this.edit(row, column) }>编辑</ht-base-button>
                            ]
                          }
                          else{
                            return [
                              <ht-base-button type = "primary" onClick={ () => this.edit(row, column) }>编辑</ht-base-button>,
                              <ht-base-button style="margin-left:10px" type="primary" ghost onClick={ () => this.remove(row, column) }>删除</ht-base-button>
                            ]
                          }
                        }
                      }
                    },
                    {
                      field: 'name',
                      title: '接口名',
                      width: 140,
                      align: "center",
                    },
                    {
                        field: 'clientId',
                        title: '客户端ID',
                        width: 140,
                        align: "center",
                    },
                    {
                      field: 'secretKey',
                      title: 'SecretKey',
                      width: 150,
                      align: "center",
                    },
                    {
                        field: 'baiduTokenUrl',
                        title: '百度Token接口地址',
                        align: "left",
                        width: 180,
                    },
                    {
                        field: 'baiduIocrUrl',
                        title: '百度IOCR接口地址',
                        align: "center",
                        width: 180,
                    },
                    {
                      field: 'templateSign',
                      title: '模板ID',
                      align: "center",
                      width: 300,
                    },
                    {
                        field: 'isDelete',
                        title: '是否删除',
                        align: "center",
                        width:100,
                        slots: {
                            default: ({row, column}) => {
                                if (row.isDelete == 0) {
                                    return [
                                        <div>正常</div>
                                    ]
                                } else{
                                    return [
                                        <div style="color:red;">删除</div>
                                    ]
                                }
                            }
                        }
                    },
                    {
                        field: 'createDate',
                        title: '创建时间',
                        align: "center",
                        width:150,
                        slots: {
                            default: ({row, column}) => {
                               if(row.createDate != null){
                                   return row.createDate.substring(0, 10)
                               }
                            }
                        }
                    },
                    {
                      field: 'modifyDate',
                      title: '修改时间',
                      align: "center",
                      width:150,
                      slots: {
                        default: ({row, column}) => {
                          if(row.modifyDate != null){
                            return row.modifyDate.substring(0, 10)
                          }
                        }
                      }
                    }
                ],
            }
        },
        created() {
            this.tableHeight = window.innerHeight - 180;
            this.findList();  //table数据
        },
        methods: {
            //table数据
            findList() {
              this.loading = true;
                queryApi('api/config/getConfigList', {
                    pageNo: this.tablePage.currentPage,
                    pageSize: this.tablePage.pageSize,
                    clientId: this.clientId,
                    templateSign: this.templateSign
                }).then((res) => {
                  if (res.total === 0 || res.total === null) {
                    this.tableData = []
                    this.tablePage.total = 0;
                    this.tablePage.currentPage = 1;
                  } else {
                    this.tablePage.total = res.total;
                    this.tableData = res.list;
                  }
                  this.loading = false;
                });
            },

            //分页跳转
            handlePageChange({currentPage, pageSize}) {
                this.tablePage.currentPage = currentPage;
                this.tablePage.pageSize = pageSize;
                this.findList()
            },
            //搜索
            queryList() {
                this.tablePage.currentPage=1;
                this.findList();
            },
            //新增
            add(name) {
                this.addModal = true;
                this.formValidate.id = '';
                this.formValidate.name = '';
                this.formValidate.clientId = '';
                this.formValidate.secretKey = '';
                this.formValidate.baiduTokenUrl = '';
                this.formValidate.baiduIocrUrl='';
                this.formValidate.templateSign = '';
                this.business="新增配置信息";
                this.$refs[name].resetFields();
            },
            handleSubmit(name) {
                if (this.formValidate.id != "") {
                    this.$refs[name].validate((valid) => {
                        if (valid) {
                            queryApi('api/config/editConfig', (this.formValidate)).then((res) => {
                                if (res !== undefined) {
                                    this.addModal = false;
                                    this.$refs[name].resetFields();
                                    this.$HtBaseMessage.success('修改成功!');
                                    this.findList();
                                } else {
                                    this.$HtBaseMessage.error('修改失败!');
                                }
                            });
                        }
                    })
                } else {
                    this.$refs[name].validate((valid) => {
                        if (valid) {
                            queryApi('api/config/addConfig', (this.formValidate)).then((res) => {
                                if (res !== undefined) {
                                    this.$refs[name].resetFields();
                                    this.$HtBaseMessage.success('添加成功!');
                                    this.addModal = false;
                                    this.findList();
                                } else {
                                    this.$HtBaseMessage.error('添加失败!');
                                }
                            });
                        }
                    })
                }
            },
            //新增修改关闭modal
            cancel(name) {
                this.$refs[name].resetFields();
                this.addModal = false;
            },
            //编辑
            edit(row) {
                this.$refs['formValidate'].resetFields();
                this.addModal = true;
                this.business="编辑配置信息";
                this.formValidate.id = row.id;
                queryApi('api/config/queryById', {
                    id: this.formValidate.id,
                }).then((res) => {
                    if (res !== false) {
                        this.formValidate.id = this.formValidate.id;
                        this.formValidate.clientId = res.clientId;
                        this.formValidate.name = res.name;
                        this.formValidate.secretKey = res.secretKey;
                        this.formValidate.baiduTokenUrl = res.baiduTokenUrl;
                        this.formValidate.baiduIocrUrl = res.baiduIocrUrl;
                        this.formValidate.templateSign = res.templateSign;
                    }
                });
            },

            //删除
            remove(row) {
                let that=this;
                var ids = [];
                ids.push(row.id);
                that.$HtBaseModal.confirm({
                    title: "您确认要删除么？",
                    onOk() {
                        queryApi('api/config/deleteById', {
                            ids: JSON.parse(ids),
                        }).then((res) => {
                            if (res != false) {
                              that.findList();
                              this.$HtBaseMessage.success('删除成功!');
                            }
                        });
                    },
                    cancelText: '取消',
                });
            }
        }
    }
</script>

<style scoped xml:lang="less">
  .modal-form{
    height: 350px;
  }
  .table {
    overflow: scroll;
  }

  .search {
    float: right;
    margin-right: 10px;
  }

  .button {
    width: 100%;
    display: block;
    padding: 17px 0;
  }
</style>



