import axios from 'axios'
import qs from 'qs'
import {Notice} from 'iview'
import router from "../router";
import {queryApi} from "../service/api"

Notice.config({
  duration: 3
});
//mock地址
const mockUrl = '';
let domain = '';
const path = '/';//根据项目修改

if (process.env.NODE_ENV === "development") {
  //本地开发可以随意修改
  domain = window.g.ApiServerUrl;
} else {
  // domain = '';
  domain = window.g.ApiServerUrl;
}
const serverUrl = domain + path;
//axios默认参数配置
axios.defaults.timeout = 1000 * 50;
//添加一个请求拦截器
axios.interceptors.request.use(function (config) {
  if ( config.url == window.g.ApiServerUrl + "//busi/addCostBatchByUser") {
    if (config.method === "post") {
      config.headers['Content-Type'] = 'application/json;charset=utf-8';
      config.headers['X-Requested-With'] = 'XMLHttpRequest';
    }
    return config;
  }else{
    if (config.method === "post") {
      config.data = qs.stringify(config.data);
      config.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';
      config.headers['X-Requested-With'] = 'XMLHttpRequest';
    }
    return config;
  }
}, function (error) {
  return Promise.reject(error);
});
//添加一个响应拦截器
axios.interceptors.response.use(function (res) {
  let data = res.data;
  return new Promise((resolve) => {
    if (data.code === '1') {
      resolve(data.data);
    }
    else if(data.code === '1001'){
      Notice.error({
        title: '系统提示',
        desc: data.msg ? data.msg : '网络繁忙！'
      });
      setTimeout(function(){
        window.location.href = '../../../static/login/index.html'
      }, 300);
    }
    else {
      Notice.error({
        title: '系统提示',
        desc: data.msg ? data.msg : '网络繁忙！'
      });
      resolve();
    }
  }).catch(error => {
    console.log(error);
  });
}, function (error) {

});

export default function request(url, option) {
  let httpUrl;
  if (!option.noPrefix) {
    httpUrl = option.mock ? mockUrl + url : serverUrl + url;
  } else {
    httpUrl = option.mock ? mockUrl + url : url;
  }
  let reqUrl = '';
  if (option.body.method === 'GET' && option.body.data) {
    reqUrl = httpUrl + '?' + qs.stringify(option.body.data);
  } else {
    reqUrl = httpUrl;
  }
  const defaultOptions = {
    method: 'POST',
    url: reqUrl
  };
  const newOptions = {...defaultOptions, ...option.body};
  // console.log(newOptions)
  return axios({
    ...newOptions
  }).then(res => {
    return res
  })
}

