// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
//加载vue核心及其他生态库
import 'babel-polyfill'
import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App';
import store from './store';
import router from './router';
import 'xe-utils';
import VXETable from 'vxe-table';
import 'vxe-table/lib/index.css';
import XEUtils from 'xe-utils'
import VXEUtils from 'vxe-utils'

//加载ui框架
import iView from 'iview'
//加载自定义插件已工具函数
import lodash from 'lodash'
import moment from 'moment'
import {modifyVars} from '@tydic-group/plugin-theme-custom'
import themeConfig from './config/themeConfig'
import ruleCheck from "@tydic-group/plugin-regular-check"
import util from './libs/util'
import componentBase from '@tydic-group/component-base';
import '@tydic-group/component-base/dist/themes/default/index.less';
//加载样式
// import './styles/font/iconfont.css'
import './styles/themes/default/index.less'
import VueCookies from 'vue-cookies'

Vue.config.productionTip = false;
Vue.use(VueRouter)
window.moment = moment;
window.util=util;
window.themeConfig=themeConfig;
window.modifyVars=modifyVars;
window._ = lodash;
window.ruleCheck = ruleCheck;
Vue.use(iView);
Vue.use(componentBase);
Vue.use(VXETable)
Vue.use(VueCookies)
Vue.config.devtools = true;
Vue.use(VXEUtils, XEUtils, {mounts: ['cookie']})

router.beforeEach((to, from, next) => {
  if (to.matched.length === 0) {
    next('/staff');
  } else {
    next();
  }
});

router.afterEach(route => {

});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
});



