        created() {
            this.getUserInfo();
            this.date = this.dateFormat("YYYY-mm-dd", new Date());
            this.tableWidth = window.innerHeight - 184;
            this.tableHeight = window.innerHeight - 180;
            this.findList();
        },
        methods: {
          getUserInfo() {
            queryApi('api/user/getUserInfo', {}).then((res) => {
              if (res !== undefined) {
                if(res.roleId === 0){
                  this.isAdmin = false;
                }
                else{
                  this.isAdmin = true;
                }
              }
            })
          },
            //table数据
            findList() {
              this.loading = true;
              if(this.date != ""){
                this.date = moment(new Date(this.date)).format("YYYY-MM-DD");
              }
                queryApi('api/commodity/getCommodityList', {
                    pageNo: this.tablePage.currentPage,
                    pageSize: this.tablePage.pageSize,
                    userName: this.userName,
                    receiptNo: this.receiptNo,
                    productNumber: this.productNumber,
                    date: this.date,
                    errorNumberBegin:this.errorNumberBegin,
                    errorNumberEnd:this.errorNumberEnd
                }).then((res) => {
                  console.log(this.tablePage);
                    if (res.total === 0 || res.total === null) {
                        this.tableData = []
                        this.tablePage.total = 0;
                        this.tablePage.currentPage = 1;
                    } else {
                        this.tablePage.total = res.total;
                        this.tableData = res.list;
                    }
                  this.loading = false;
                });
            },
            getDate(selectDate){
                this.date = selectDate;
            },
            search() {
              this.loading = true;
              if(this.date != ""){
                this.date = moment(new Date(this.date)).format("YYYY-MM-DD");
              }
              queryApi('api/commodity/getCommodityList', {
                pageNo: 1,
                pageSize: this.tablePage.pageSize,
                userName: this.userName,
                receiptNo: this.receiptNo,
                productNumber: this.productNumber,
                date: this.date,
                errorNumberBegin:this.errorNumberBegin,
                errorNumberEnd:this.errorNumberEnd
              }).then((res) => {
                if (res.total === 0 || res.total === null) {
                  this.tableData = []
                  this.tablePage.total = 0;
                  this.tablePage.currentPage = 1;
                } else {
                  this.tablePage.total = res.total;
                  this.tableData = res.list;
                  this.tablePage.currentPage = 1;
                }
                this.loading = false;
              });
            },
            //分页跳转
            handlePageChange({currentPage, pageSize}) {
              this.tablePage.currentPage = currentPage;
              this.tablePage.pageSize = pageSize;
              this.findList()
            },
            statistics(){
              this.statisticsModal = true;
              this.business="统计结果";
              this.$refs['formValidate'].resetFields();
              if(this.date != ""){
                this.date = moment(new Date(this.date)).format("YYYY-MM-DD");
              }
              queryApi('api/commodity/getStatisticsInfo', {
                userName:this.userName,
                date:this.date,
                receiptNo:this.receiptNo,
                errorNumberBegin:this.errorNumberBegin,
                errorNumberEnd:this.errorNumberEnd
              }).then((res) => {
                if (res !== false) {
                  this.formValidate.product1Number = res.product1Number === 0 ? "" : res.product1Number;
                  this.formValidate.product2Number = res.product2Number === 0 ? "" : res.product2Number;
